<template>
  <div class="forgetPass">
    <!-- 设置密码页面 -->
    <!-- 头部 -->
    <div class="header">
      <p></p>
      <p>设置密码</p>
      <p @click="$router.push('/index')">跳过</p>
    </div>
    <!-- 找回密码 -->
    <van-form @submit="onSubmit">
      <van-field v-model="sms_code" name="验证码" label placeholder="请设置登录密码" />
      <van-field v-model="password" name="密码" label placeholder="请在次输入密码" />
      <div style="margin: 0.3rem;">
        <van-button round block type="warning" native-type="submit">确定</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { AjaxPass } from "@/utils/myApi"; 
export default {
  // 组件名称
  name: "",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      sms_code: "设置密码功能还没写",
      password: "",
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    async onSubmit(values) {
      let res = await AjaxPass({
        mobile: this.mobile,
        password: this.password,
        sms_code: this.sms_code,
        type: this.type
      });
      // 修改密码
      console.log("submit", values);
      console.log(res);
      if (res.code == 200) {
        this.$toast.success("修改成功");
        this.$router.push("/login");
      }
    },
    // 验证码
    async editPass() {
      let res = await AjaxSmsLogin({
        mobile: this.mobile,
        sms_type: this.sms_type
      });
      if (res.code == 200) {
        this.$toast.success("发送成功");
      }
      console.log(res);
    }
  },
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  mounted() {}
};
</script> 

<style scoped lang="scss">
.forgetPass {
  width: 100%;
  height: 100%;
  background-color: #fff;
  .header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 0.1rem;
    box-sizing: border-box;
    height: 0.45rem;
    align-items: center;
    background-color: #fff;
    font-size: 0.18rem;
  }
  .van-form {
    .van-field:hover {
      border-bottom: 1px solid #eb6100;
    }
    .van-field {
      line-height: 0.4rem;
    }
  }
}
</style>
